* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 100%;
  line-height: 1.5;
}

body {
  color: #333;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
}

img, video {
  max-width: 100%;
  height: auto;
  display: block;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button, input, textarea, select {
  font: inherit;
  background: none;
  border: none;
  outline: none;
  padding: 0;
}

.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

.header {
  background-color: #7695fb;
  padding-top: 15px;
  padding-bottom: 15px;
}

.search-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.search-form__link:hover .search-form__logo {
  opacity: 1;
  scale: 1.09;
}

.search-form__logo {
  opacity: .6;
  transition: all .2s linear;
}

.search-wrapper {
  border-radius: 15px;
  transition: box-shadow .2s;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 27px 5px rgba(0, 0, 0, .54);
}

input {
  background-color: #fff;
  padding: 10px;
}

.search-wrapper:has(input:focus) {
  box-shadow: 0 0 33px 13px rgba(0, 0, 0, .8);
}

.form-button {
  background-color: #d8d4d4;
  align-items: center;
  gap: 5px;
  padding: 10px;
  transition: all .1s;
  display: flex;
}

.form-button:hover {
  background-color: #05f;
}

.form-button:active {
  color: #d8d4d4;
  background-color: #00297c;
}

.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-top: 10px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  width: calc(50% - 5px);
  background-color: #f0f0f0;
  border-radius: 8px;
  font-size: max(7px, min(.9vw, 16px));
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.photo-card img {
  aspect-ratio: 4 / 3;
  object-fit: cover;
}

.info {
  justify-content: space-around;
  padding: 8px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: inline-flex;
}

.loader {
  width: 70px;
  aspect-ratio: 1;
  --_m: conic-gradient(rgba(0, 0, 0, 0) 10%, #000), linear-gradient(#000 0 0) content-box;
  -webkit-mask: var(--_m);
  -webkit-mask: var(--_m);
  mask: var(--_m);
  -webkit-mask-composite: source-out;
  background: #7695fb;
  border-radius: 50%;
  margin: 100px auto;
  padding: 8px;
  animation: l3 1s linear infinite;
  -webkit-mask-composite: source-out;
  mask-composite: subtract;
}

@keyframes l3 {
  to {
    transform: rotate(1turn);
  }
}

.load-more {
  background-color: #d8d4d4;
  border: 2px solid #05f;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 30px auto;
  padding: 10px;
  transition: all .1s;
  display: flex;
}

.load-more:hover {
  background-color: #05f;
  box-shadow: 0 0 27px 5px rgba(0, 0, 0, .54);
}

.load-more:active {
  color: #d8d4d4;
  background-color: #00297c;
  box-shadow: 0 0 33px 13px rgba(0, 0, 0, .8);
}

.hidden {
  display: none;
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }

  .search-form {
    flex-direction: row;
    gap: 20px;
  }

  .photo-card {
    width: calc(25% - 7.5px);
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1200px;
  }
}

@media (min-width: 1920px) {
  .container {
    max-width: 1400px;
  }
}

